.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.bigred {
  font-family:"Courier New";
  font-style: italic;
  font-size: xx-large;
  color: red;
}

.blueTitle {
  font-style: italic;
  font-size: 3.9vw;
  color: #00BCD4;
}
.alertText {
  color: #FF1744;
  font-size: 2.9vw;
  margin: 3%;
}

.lamp {

  height: 30px;
  width: 30px;
  border-style: solid;
  border-width: 2px;
  border-radius: 25px;
}


.MaDiv {

  float: left;
  margin-right: 100px;
  margin-bottom: 10px;
}


